.content-col-img {
  padding-top: 100px;
  padding-bottom: 100px;
}
@media screen and (min-width: 768px) {
  .content-col-img.image-right .container > .row {
    flex-direction: row-reverse;
    justify-content: space-between;
  }
}
@media screen and (max-width: 1024px) {
  .content-col-img .img-wrap {
    margin-bottom: 32px;
  }
}
.content-col-img .info-white-section-img {
  position: relative;
  z-index: 2;
}
.content-col-img .circle-purple {
  left: inherit;
  border-radius: 50%;
  background-color: #fc4a6a;
  top: 0;
  right: 68%;
  z-index: 3;
  opacity: 0.7;
}
@media screen and (max-width: 1024px) {
  .content-col-img .circle-purple {
    display: none;
  }
}
.content-col-img .circle-border {
  left: inherit;
  border-radius: 50%;
  border: 1px solid rgba(27, 155, 203, 0.3);
  top: 73.5%;
  right: 68%;
  z-index: 2;
}
@media screen and (max-width: 1024px) {
  .content-col-img .circle-border {
    display: none;
  }
}
.content-col-img .circle-gray {
  left: inherit;
  border-radius: 50%;
  background-color: #f2f6fb;
  top: 73.3%;
  right: 0;
  z-index: 1;
}
@media screen and (max-width: 1024px) {
  .content-col-img .circle-gray {
    display: none;
  }
}
.content-col-img .img-width-wrap {
  position: relative;
  z-index: 2;
}
@media screen and (max-width: 1024px) {
  .content-col-img .img-width-wrap {
    width: 100%;
    height: 0;
    padding-bottom: 100%;
  }
}
.content-col-img .img-wrap {
  position: relative;
}
@media screen and (max-width: 1024px) {
  .content-col-img .img-wrap {
    max-width: 400px;
    margin-left: auto;
    margin-right: auto;
  }
}
@media screen and (min-width: 1025px) {
  .content-col-img .img-wrap {
    width: 28.646vw;
    height: 28.646vw;
  }
}
@media screen and (min-width: 1921px) {
  .content-col-img .img-wrap {
    width: 550px;
    height: 550px;
  }
}
.content-col-img .max-w-container {
  display: flex;
  align-items: flex-start;
}
@media screen and (max-width: 1024px) {
  .content-col-img .max-w-container {
    flex-flow: row wrap;
  }
}
.content-col-img .txt-size-60 {
  margin-bottom: 25px;
}
@media screen and (min-width: 1025px) {
  .content-col-img .txt-size-60 {
    margin-bottom: 1.615vw;
  }
}
@media screen and (min-width: 1921px) {
  .content-col-img .txt-size-60 {
    margin-bottom: 31px;
  }
}
@media screen and (min-width: 1025px) {
  .content-col-img .txt-post-rtf {
    padding-right: 2.083vw;
  }
}
@media screen and (min-width: 1921px) {
  .content-col-img .txt-post-rtf {
    padding-right: 40px;
  }
}
.content-col-img .txt-post-rtf ul {
  list-style-type: none;
}
@media screen and (max-width: 550px) {
  .content-col-img .txt-post-rtf ul {
    margin: 6vw 0vw;
  }
}
@media screen and (min-width: 551px) and (max-width: 1024px) {
  .content-col-img .txt-post-rtf ul {
    margin: 30px 0;
  }
}
@media screen and (min-width: 1025px) {
  .content-col-img .txt-post-rtf ul {
    margin: 1.563vw 0vw;
  }
}
@media screen and (min-width: 1921px) {
  .content-col-img .txt-post-rtf ul {
    margin: 30px 0;
  }
}
.content-col-img .txt-post-rtf ul li {
  padding-left: 17px;
  position: relative;
}
.content-col-img .txt-post-rtf ul li::before {
  content: "";
  position: absolute;
  display: inline-block;
  background-color: #002ea6;
  border-radius: 50%;
  width: 6px;
  height: 6px;
  top: 0.65em;
  left: 4px;
}
.content-col-img .txt-post-rtf h2,
.content-col-img .txt-post-rtf .h2 {
  line-height: 1.2;
  margin-bottom: 15px;
}
@media screen and (max-width: 550px) {
  .content-col-img .txt-post-rtf h2,
  .content-col-img .txt-post-rtf .h2 {
    font-size: 6vw;
  }
}
@media screen and (min-width: 551px) and (max-width: 1024px) {
  .content-col-img .txt-post-rtf h2,
  .content-col-img .txt-post-rtf .h2 {
    font-size: 30px;
  }
}
@media screen and (min-width: 1025px) {
  .content-col-img .txt-post-rtf h2,
  .content-col-img .txt-post-rtf .h2 {
    font-size: 1.563vw;
    margin-bottom: 2.083vw;
  }
}
@media screen and (min-width: 1921px) {
  .content-col-img .txt-post-rtf h2,
  .content-col-img .txt-post-rtf .h2 {
    font-size: 30px;
    margin-bottom: 40px;
  }
}
@media screen and (min-width: 1025px) {
  .content-col-img .txt-post-rtf p {
    margin-left: 0.156vw;
  }
}
@media screen and (min-width: 1921px) {
  .content-col-img .txt-post-rtf p {
    margin-left: 3px;
  }
}
@media screen and (max-width: 550px) {
  .content-col-img .txt-post-rtf p:not(:last-of-type) {
    margin-bottom: 6.4vw;
  }
}
@media screen and (min-width: 551px) and (max-width: 1024px) {
  .content-col-img .txt-post-rtf p:not(:last-of-type) {
    margin-bottom: 32px;
  }
}
@media screen and (min-width: 1025px) {
  .content-col-img .txt-post-rtf p:not(:last-of-type) {
    margin-bottom: 1.667vw;
  }
}
@media screen and (min-width: 1921px) {
  .content-col-img .txt-post-rtf p:not(:last-of-type) {
    margin-bottom: 32px;
  }
}
.content-col-img .customTheme-block .column-image {
  width: 100%;
}
.content-col-img .customTheme-block .block-heading,
.content-col-img .customTheme-block .block-subheading {
  margin-bottom: 24px;
}
.content-col-img .circle-blue {
  left: inherit;
  border-radius: 50%;
  background-color: #002ea6;
  top: 0;
  right: 68%;
  z-index: 3;
  opacity: 0.2 !important;
}
.content-col-img .circle-gray {
  right: 68% !important;
  background-color: #002ea6 !important;
  opacity: 0.05 !important;
}
.content-col-img .circle-border {
  right: 0 !important;
}
@media screen and (max-width: 1024px) {
  .content-col-img .circle-blue {
    display: none;
  }
  .content-col-img .customTheme-block .block-heading {
    margin-bottom: 16px;
  }
}